import { useTranslation } from "react-i18next";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import CombinedTeaching from "./CombinedTeaching";
import CombinedOtherTeaching from "./CombinedOtherTeaching";
import CombinedNonTeaching from "./CombinedNonTeaching";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useCallback } from "react";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
const LmsAllottment = ({ instituteId, staffAlias }) => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();

  const onNavigate = useCallback(() => {
    let url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/lms`
        : `/q/${params.username}/member/staff/lms`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);
  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <div className={style.fm_header_start_container}>
            <img
              alt="search icon"
              src={`${assestsUtilityUrl}/back.svg`}
              style={{
                height: "2rem",
                width: "2rem",
              }}
              onClick={onNavigate}
            />
            <h6>{t("leave_allottment")}</h6>
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <HorizontalTabWrapper>
        {tabList.map((tab) => (
          <HorizontalTabCard
            key={tab?.id}
            tab={tab}
            isLink
            isActive={tab.groupIs?.includes(searchParams.get("n"))}
            parentState={getQuery.state}
            url={
              getQuery.state?.urlOffSet === "Institute"
                ? `/q/${params.username}/institute/member/lms?type=allottment${tab.urlKey}`
                : `/q/${params.username}/member/staff/lms?type=allottment${tab.urlKey}`
            }
          />
        ))}
      </HorizontalTabWrapper>
      <BorderBottom
        customStyleBorder={{
          marginBlock: "0",
        }}
      />
      <UserMemberContentWrapper>
        {!searchParams.get("n") ? (
          <CombinedTeaching instituteId={instituteId} staffAlias={staffAlias} />
        ) : searchParams.get("n") === "nonteaching" ? (
          <CombinedNonTeaching
            instituteId={instituteId}
            staffAlias={staffAlias}
          />
        ) : searchParams.get("n") === "other" ? (
          <CombinedOtherTeaching
            staffAlias={staffAlias}
            instituteId={instituteId}
          />
        ) : null}
      </UserMemberContentWrapper>
    </UserMemberContainerWrapper>
  );
};

export default LmsAllottment;
const tabList = [
  {
    label: "combined_teaching",
    id: "combined_teaching",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "non_teaching",
    id: "non_teaching",
    urlKey: "&n=nonteaching",
    groupIs: ["nonteaching"],
  },
  {
    label: "other",
    id: "other",
    urlKey: "&n=other",
    groupIs: ["other"],
  },
];
