import { useNavigate, useParams } from "react-router-dom";
import { useTranslation } from "react-i18next";
import style from "../LmsManager.module.css";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
const CombinedTeachingCard = ({ staff, staffAlias }) => {
  const { t } = useTranslation();
  const params = useParams();
  const navigate = useNavigate();

  const onNavigate = () => {
    navigate(`/q/${params.username}/staff/profile`, {
      state: {
        sid: staff?._id,
        viewAs: "DEPARTMENT_HEAD",
      },
    });
  };

  return (
    <>
      <div
        className={style.lms_staff_card}
        onClick={onNavigate}
        title="View Staff Profile"
      >
        <div className={style.lms_staff_card_contatiner}>
          <img
            src={
              staff?.staffProfilePhoto
                ? `${imageShowUrl}/${staff?.staffProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
            alt="avatar"
          />

          <div className={style.lms_staff_card_contatiner_inner}>
            <h6>
              {`${staff?.staffFirstName ?? ""} ${
                staff?.staffMiddleName ?? ""
              } ${staff?.staffLastName ?? ""}`}
            </h6>
            <p>
              {staffAlias ? staffAlias : t("index_number")}
              {": "}
              {staff?.staffROLLNO ?? ""}
            </p>
          </div>
        </div>
      </div>
    </>
  );
};

export default CombinedTeachingCard;
