import { useCallback, useEffect, useState } from "react";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import CombinedTeachingCard from "./CombinedTeachingCard";
import { useLmsAllottmentStaffList } from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import { useTranslation } from "react-i18next";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";

const CombinedTeaching = ({ instituteId, staffAlias }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { lmsAllottmentStaffList, lmsAllottmentStaffListLoading } =
    useLmsAllottmentStaffList({
      data: {
        id: instituteId,
        page: page,
        limit: 10,
        type: "Teaching Faculty",
        search: search,
      },
      skip: !instituteId,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (lmsAllottmentStaffList?.list?.length) {
      let obj = nextPage(lmsAllottmentStaffList?.list?.length);
      setState(() => obj);
    }
  }, [lmsAllottmentStaffList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  return (
    <>
      <div
        className={style.fm_search_full}
        style={{
          marginBottom: "0.7rem",
        }}
      >
        <section className={style.fm_search_full_container}>
          <div className={style.fm_search_full_container_inner}>
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
            />
          </div>
        </section>
      </div>
      {lmsAllottmentStaffList?.list?.map((staff, index) =>
        lmsAllottmentStaffList?.list?.length === index + 1 ? (
          <div key={staff?._id} ref={ref}>
            <CombinedTeachingCard staff={staff} staffAlias={staffAlias} />
          </div>
        ) : (
          <CombinedTeachingCard
            staff={staff}
            key={staff?._id}
            staffAlias={staffAlias}
          />
        )
      )}
      {lmsAllottmentStaffListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        lmsAllottmentStaffList?.list?.length === 0 && (
          <EmptyMessage title="No teaching staff fuond related this search." />
        )
      ) : (
        lmsAllottmentStaffList?.list?.length === 0 && (
          <EmptyMessage title="No teaching staff." />
        )
      )}
    </>
  );
};

export default CombinedTeaching;
